<template>
  <div class="detail">
    <div class="detail-img">
      <div class="pro-img">
        <img :src="product.large_img" alt />
      </div>

      <div class="pro-name">
        <div class="ch">{{product.name}}</div>
        <div class="en">{{product.enname}}</div>
      </div>

      <div class="pro-like" @click="likeOrNot">
        <van-icon name="like" :style="{color:isLike?'#ff770f':'#f0f0f0'}" />喜欢
      </div>
    </div>

    <div class="pro-info">
      <!-- 关闭 -->
      <van-icon name="cross" class="close" @click="goBack" />

      <!-- 规格 -->
      <div class="pro-rules">
        <div class="rule" v-for="(item, index) in product.rules" :key="index">
          <div class="rule-title">{{item.title}}</div>
          <div class="rule-items">
            <div
              class="item"
              v-for="(val, ind) in item.rules"
              :key="ind"
              :class="{active:val.isActive}"
              @click="toggleRule(val,index)"
            >{{val.title}}</div>
          </div>
        </div>
      </div>

      <!-- 介绍 -->
      <div class="pro-desc">
        <div class="desc-title">商品介绍</div>
        <div class="desc-item" v-for="(item, index) in product.desc" :key="index">{{item}}</div>
      </div>
    </div>

    <!-- 加入购物车 -->
    <div class="addToShopbag">
      <div class="count">
        <span class="sub" @click="sub">-</span>
        <input class="inp" type="text" maxlength="2" v-model="count" @input="inp" />
        <span class="add" @click="add">+</span>
      </div>

      <div class="addto" @click="addToCart">加入购物车 ￥{{sum}}</div>
    </div>
  </div>
</template>

<script>
import "../assets/less/detail.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      product: {},

      pid: "",
      count: 1,
      isLike: false,
    };
  },

  computed: {
    sum() {
      return (this.product.price * this.count).toFixed(2);
    },
  },

  methods: {
    // 返回
    goBack() {
      this.$router.go(-1);
    },

    // 跳到购物袋
    goCart() {
      this.$router.push({ name: "Cart" });
    },

    // 根据商品ID获取商品数据
    getProductByPid() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 600) {
            result.data.result[0].desc = result.data.result[0].desc.split(/\n/);

            let ruleItems = ["tem", "sugar", "cream", "milk"];
            let rulesData = [];
            ruleItems.map((v) => {
              if (result.data.result[0][v] == "") {
                return;
              }

              let rule = {
                title: result.data.result[0][v + "_desc"],
                rules: [],
              };

              let item = result.data.result[0][v].split("/");
              item.map((val, ind) => {
                let o = {
                  title: val,
                  isActive: ind == 0,
                };

                rule.rules.push(o);
              });

              rulesData.push(rule);
            });

            result.data.result[0].rules = rulesData;

            this.product = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 切换规格
    toggleRule(val, ind) {
      if (val.isActive) {
        return;
      }

      let ruleData = this.product.rules[ind].rules;

      for (let i = 0; i < ruleData.length; i++) {
        if (ruleData[i].isActive) {
          ruleData[i].isActive = false;
          break;
        }
      }

      val.isActive = true;
    },

    // 查询商品是否被收藏
    findLike() {
      let tokenString = localStorage.getItem("owner");

      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          pid: this.pid,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 1000) {
            if (result.data.result.length == 1) {
              this.isLike = true;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 收藏
    likeOrNot() {
      let tokenString = localStorage.getItem("owner");

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let data = utils.queryString({
        appkey: this.appkey,
        pid: this.pid,
        tokenString,
      });

      let url = this.isLike ? "/notlike" : "/like";

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 700) {
            return this.$router.push({ name: "Login" });
          }

          this.$toast(result.data.msg);

          if (result.data.code == 800) {
            this.isLike = true;
          } else if (result.data.code == 900) {
            this.isLike = false;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 添加到购物车
    addToCart() {
      if (this.count == '0') {
        return this.$toast('请添加数量')
      }

      let tokenString = localStorage.getItem("owner");

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let rules = this.product.rules;

      let rule = [];

      rules.map((v) => {
        for (let i = 0; i < v.rules.length; i++) {
          if (v.rules[i].isActive) {
            rule.push(v.rules[i].title);
            break;
          }
        }
      });

      let data = utils.queryString({
        appkey: this.appkey,
        count: this.count,
        pid: this.pid,
        rule: rule.join("/"),
        tokenString,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 3000) {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    sub() {
      if (this.count <= 1) {
        return (this.count = 1);
      }
      this.count--;
    },

    inp() {
      this.count = this.count.match(/\d+/);
      this.count = Math.abs(this.count);
      // if (this.count == "") {
      //   this.count = "1";
      // }
    },

    add() {
      
      if (this.count >= 99) {
        return;
      }
      this.count++;
    },
  },

  created() {
    this.pid = this.$route.query.pid;

    this.getProductByPid();
    this.findLike();
  },
};
</script>